.work-projects {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  margin-top: 1.5rem;
}

.project-card {
  background-color: var(--card-bg);
  border-radius: 12px;
  transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  overflow: hidden;
}

.project-card:hover {
  transform: translateY(-5px) scale(1.02);
}

.project-layout-split {
  display: flex;
  align-items: center;
  padding: 0;
  gap: 2rem;
}

.project-details {
  flex: 1 1 50%;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
}

.project-image-container {
  flex: 1 1 50%;
  display: flex;
  aspect-ratio: 16 / 10;
  max-height: 300px;
}

.project-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.8rem;
  text-align: left;
}

.project-description {
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 1.5rem;
  flex-grow: 1;
}

.project-tags {
  display: flex;
  gap: 0.8rem;
  margin-top: auto;
}

.tag {
  background-color: rgba(128, 128, 128, 0.2);
  color: var(--text-color);
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-size: 0.8rem;
  font-weight: 500;
  white-space: nowrap;
}

.tag-secondary {
  background-color: #a0522d;
  color: #f5f5f5;
  opacity: 0.8;
}

.random-projects .project-card {
   padding: 1.5rem;
}

.project-detail-page {
  max-width: 800px;
  margin: 2rem auto 4rem auto;
  padding: 0 1rem;
}

.back-link {
  display: inline-block;
  margin-bottom: 2rem;
  color: var(--text-color);
  text-decoration: none;
  opacity: 0.8;
  transition: opacity 0.3s;
}

.back-link:hover {
  opacity: 1;
}

.project-content h1 {
  font-size: 2.5rem;
  margin-bottom: 0.75rem;
  line-height: 1.2;
}

.project-summary {
  font-size: 1.2rem;
  margin-bottom: 3rem;
  opacity: 0.9;
  line-height: 1.5;
}

.project-section {
  margin-bottom: 2.5rem;
}

.project-section h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid rgba(128, 128, 128, 0.3);
  padding-bottom: 0.5rem;
}

.project-section p {
  font-size: 1rem;
  line-height: 1.7;
  margin-bottom: 1rem;
}

.project-section ul,
.project-section ol {
  margin-left: 1.5rem;
  margin-bottom: 1rem;
}

.project-section li {
  margin-bottom: 0.5rem;
}
